import React, { useEffect } from 'react';
import Isvg from 'react-inlinesvg';
import { Title } from '../components/Title';
import { DefaultLayout } from '../containers/DefaultLayout';
import ContactCover from '../images/contact.png';
import Mail from '../images/svg/mail.svg';
import Fb from '../images/svg/social-networks/sidebar/fb.svg';
import Insta from '../images/svg/social-networks/sidebar/insta.svg';
import Pintrest from '../images/svg/social-networks/sidebar/pintrest.svg';
import TikTok from '../images/svg/social-networks/sidebar/tiktok.svg';
import Twitter from '../images/svg/social-networks/sidebar/twitter.svg';



export const Contact = () => {

    // Inject vbout script
    useEffect(() => {
        const script = document.createElement("script");
        script.src = "https://www.vbt.io/ext/vbtforms.js?lang=en";
        script.async = true;
        document.body.appendChild(script);
    }, []);

    return (
        <div className="contact-wrapper">
            <DefaultLayout coverImage={ContactCover} coverText="Contact Us">
                <div>
                    <div className="contact">
                        <div className="col-left">
                            <Title text="Contact form" />
                            <div className="contact-form" dangerouslySetInnerHTML={{
                                __html: `<form action="https://www.vbt.io/embedcode/submit/74937/?_format=page" target="_blank" method="post" data-vboutform="74937" id="vboutEmbedForm-74937" name="vboutEmbedForm-74937" enctype="multipart/form-data">
                                            <input type="text" name="vbout_EmbedForm[field][408613]" id="custom-408613" value="" placeholder="Name" />
                                            <input type="email" name="vbout_EmbedForm[field][408616]" id="custom-408616" value="" data-error="" placeholder="Email"/>
                                            <input type="text" name="vbout_EmbedForm[field][408617]" id="custom-408617" value="" placeholder="Phone" />
                                            <textarea name="vbout_EmbedForm[field][408629]" id="custom-408629" value="" data-error="" placeholder="Message"></textarea>			
                                            <button type="submit">Submit</button>
                                        </form>`
                            }}>
                            </div>
                        </div>
                        <div className="col-right">
                            <Title text="How can you contact us" />
                            <div className="contact-info">
                                <span><Isvg src={Mail} /> <a href="mailto:info@mallorcamagic.es">info@mallorcamagic.es</a></span>
                            </div>
                            <div className="social-icons">
                                <a href="https://www.facebook.com/mallorca.is.magic" target="_blank" rel="noopener noreferrer"><Isvg src={Fb} /></a>
                                <a href="https://www.instagram.com/mallorcamagic/" target="_blank" rel="noopener noreferrer"><Isvg src={Insta} /></a>
                                <a href="https://www.pinterest.de/mallorca_magic/" target="_blank" rel="noopener noreferrer"><Isvg src={Pintrest} /></a>
                                <a href="https://twitter.com/mallorca_magic" target="_blank" rel="noopener noreferrer"> <Isvg src={Twitter} /></a>
                                <a href="https://www.tiktok.com/@mallorca.magic" target="_blank" rel="noopener noreferrer"><Isvg src={TikTok} /></a>
                            </div>

                        </div>
                    </div>
                </div>
            </DefaultLayout>
        </div>
    )
}

export default Contact